$about3D-w : 2075px;

.about {
	color: $color-black;

	p {
		font-size: 2.8em;
		line-height: 1.6em;
	}

	.icon {
		fill: $color-black;
	}

	&__container {
		width: $about3D-w;
		// max-width: 2160px;
		// margin-top: 70px;
		line-height: 1.4em;
		z-index: 10;
		text-align: left;
		user-select: none;
		font-size: 0.8em;
		// transform: translateY(50%) !important;


	}

	&__header {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: baseline;
		margin-bottom: 20px;

	}

	&__date {
		color: $color-grey;
	}

	&__descr {
		width: 100%;
		line-height: 2em;
		font-family: 'Theinhardt-light';
	}

	&__subHeader {
		width: 100%;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		margin-top: 30px;

		font-family: 'Theinhardt-light';
	}

	&__role,
	&__technos,
	&__context {
		width: 20%;

		p:first-child {
			margin-bottom: 10px;
		}

		p:last-child {
			color: $color-grey;
		}
	}

	&__link {
		width: 40%;
		display: flex;
		justify-content: flex-end;
		align-items: center;

		span {
			margin-right: 20px;
		}

		a {
			transform: rotate(90deg);

		}
	}

	&__gallery {
		margin-top: 100px;
		margin-bottom: 60px;
		position: relative;
		max-width: $about3D-w;
		width: $about3D-w;
	}

	&__image {
		width: 100%;
		// max-height: 300px;
		opacity: 0.9;
		user-select: none;
		visibility: hidden;
		margin-bottom: 40px;

		img {
			width: 100%;
			border: 1px solid rgba(128, 128, 128, 0.15);
			// transform: translateY(50%);
		}
	}

	&__btn {
		cursor: pointer;
	}

	&__footer {
		opacity: 0;
		display: flex;
		justify-content: space-between;
		align-items: baseline;
		font-size: 0.8em;
		text-align: left;
		width: $about3D-w;
		position: relative;
		// transform: translateY(50%);
	}

	&__awards {
		color: $color-grey;
	}

	&__back {
		position: absolute;
		top: 180px;
		left: 50%;
		transform: translateX(-50%);
		text-align: center;
		text-transform: uppercase;
		cursor: pointer;

		.button {
			text-transform: none;
			margin-top: 20px;
		}
	}

	&__arrow {
		img {
			width: 40px;
		}
		transition: transform 0.3s ease-out;
		cursor: pointer;
		opacity: 0;
		&:hover {
			transform: scale(1.05) translateX(10px) !important;
		}
		&-l {
			transform: rotate(180deg);
			&:hover {
				transform: scale(1.05) rotate(180deg) translateX(10px) !important;
			}
		}
		&-r {
			width: 30px;
			display: inline-block;
			vertical-align: middle;
			margin-left: 25px;
		}
	}

	&__title {
		font-size: 4.6em;
		line-height: 1.6em;
		display: inline-block;

	}

	&__number, &__more {
		position: absolute;
		color: $color-grey;
		// display: none;
		opacity: 0;
	}

	&__number {
		position: absolute;
		top: -300px;
		font-size: 3rem;
		left: 5px;
	}

	&__more {
		left: 0px;
		padding-left: 0px;
		font-size: 2.2rem;
		letter-spacing: 4px;
		font-family: 'Theinhardt-regular';
		top: 150px;
		opacity: 1;
		transition: padding 0.8s $easeToinou;
		&:after {
			content: '';
			position: absolute;
			width: 0px;
			height: 3px;
			background-color: $color-grey;
			left: 0px;
			top: 50%;
			transform: translateY(-50%);
			transition: width 0.8s $easeToinou;
		}
		// font-size: 2em;

	}

	&__next, &__prev {
		font-size: 40px;
		display: inline-block;
		vertical-align: middle;
		cursor: pointer;
		white-space: nowrap;
		opacity: 0;
		text-transform: uppercase;
		&.transi {
			transition: transform 0.6s $easeToinou;
		}
		&:hover {
			transform: scale(1.15) !important;
		}
	}

	&__prev {
		display: none;
	}



	.link {
		img {
			width: 40px;
			margin-left: 20px;
			margin-bottom: -5px;
		}
	}

	.gallery {
		&__arrow {
			width: 25px;
			height: 35px;
			display: inline-block;
			cursor: pointer;
			visibility: hidden;
			opacity: 0;
			transition: transform 0.5s $easeToinou;
			&-l {
				transform: rotate(-180deg);
				margin-right: 5px;
				opacity: 0.2;
			}
			&:hover {
				transform: scale(1.05) translateX(-5px) rotate(-180deg) !important;
			}
			&-r {
				// transform: rotate(0deg);
				margin-left: 5px;
				&:hover {
					transform: scale(1.05) translateX(5px) !important;
				}
			}
		}
	}

